import React from "react";

import { Table, useTable } from "ka-table";
import { EditingMode, PagingPosition, SortingMode } from "ka-table/enums";
import { productGrid } from "data/dummy";
import data from "data/stranco-inventory.json";

const DataTable = () => {
  const table = useTable();
  const Inventory = data.map((item, index) => ({
    ...item,
    id: index,
  }));
  return (
    <>
      <div className="flex">
       <Table
        table={table}
        columns={productGrid}
        data={Inventory}
        editingMode={EditingMode.Cell}
        rowKeyField={"id"}
        sortingMode={SortingMode.Single}
        paging= {{
          enabled: true,
          pageIndex: 0,
          pageSize: 10,
          pageSizes: [10, 30, 50],
          position: PagingPosition.Bottom
        }}
      /> 
      </div>
      
    </>
  );
};

export default DataTable;